<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">用户登录</h3>
    <el-form-item prop="email">
      <el-input type="text" v-model="ruleForm2.email" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm2.password" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <!-- <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox> -->
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:50%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      <el-button @click.native.prevent="handleRegister">注册用户</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  import { requestLogin } from '../api/api';
  //import NProgress from 'nprogress'
  export default {
    data() {
      return {
        logining: false,
        ruleForm2: {
          email: '',
          password: ''
        },
        rules2: {
          email: [
            { required: true, message: '请输入E-mail地址', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {
      handleRegister() {
        console.log("to register")
        this.$router.push({ path: '/register' });
      },
      handleSubmit2(ev) {
        var _this = this;
        
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            console.log("llllogin")
            requestLogin(this.ruleForm2).then(rsp => {
              this.logining = false;
              console.log(rsp)
              if (rsp.status !== 200) {
                this.$message({
                  message: rsp.error,
                  type: 'error'
                });
              } else {
                let user = rsp.data
                sessionStorage.setItem('user', JSON.stringify(user));
                this.$router.push({ path: '/usermodel' });
              }
            }).catch(err =>{
              this.$message({
                  message: "登录失败",
                  type: 'error'
                });
              console.log(err)
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>



